/* style for placeholder */
.place-holder {
	position: fixed;
	z-index: 100;
	opacity: 0;
	color: hsl(0, 0%, 50%);
	transition-duration: 0.2s;
	transition-property: opacity;
	transition-timing-function: ease-in, ease-out;
	margin: 0;
}

.show-place-holder {
	opacity: 1;
	transition-duration: 1s;
}

/* style for response loader */
.gpt-loader {
	position: fixed;
	width: 5px;
	height: 5px;
	aspect-ratio: 1;
	border-radius: 50%;
	background: hsl(0, 0%, 0%);
	box-shadow: 0 0 0 0 hsla(224, 100%, 3%, 0.377);
	animation: l2 1.5s infinite linear;
	opacity: 0;
}

.show-gpt-loader {
	opacity: 1;
}

.gpt-loader:before,
.gpt-loader:after {
	content: "";
	position: absolute;
	inset: 0;
	border-radius: inherit;
	box-shadow: 0 0 0 0 hsla(224, 100%, 3%, 0.377);
	animation: inherit;
	animation-delay: -0.5s;
}

.gpt-loader:after {
	animation-delay: -1s;
}

@keyframes l2 {
	100% {
		box-shadow: 0 0 0 40px hsla(224, 100%, 3%, 0)
	}
}

/* style for response error */

.response-error {
	position: fixed;
	z-index: 100;
	color: hsl(0, 0%, 100%);
	background: hsl(0, 39%, 60%);
	width: auto;
	padding: 5px 10px;
	border-radius: 5px;
	transition-duration: 1s;
	transition-property: opacity;
	opacity: 0;
	right: 10px;
	top: 0;
}

.show-response-error {
	opacity: 1;
}

/* style for slash animation */
.ck-slash {
	font-size: 18px;
	opacity: 0;
	animation: ck-fade-in-pulse 750ms ease-out forwards;
	background: hsla(60, 88%, 87%, 1);
	padding: 0px 5px;
	text-shadow:
		0 0 10px var(--ck-color-widget-hover-border),
		0 0 20px var(--ck-color-widget-hover-border),
		0 0 30px var(--ck-color-widget-hover-border),
		0 0 40px var(--ck-color-widget-hover-border);
	display: inline-block;
}

@keyframes ck-fade-in-pulse {
	0% {
		opacity: 0;
		transform: scale(1);
		color: hsl(9, 72%, 100%);
	}

	50% {
		opacity: 1;
		transform: scale(1.1);
		color: hsl(0, 0%, 100%);
		text-shadow:
			0 0 10px var(--ck-color-widget-hover-border),
			0 0 20px var(--ck-color-widget-hover-border),
			0 0 30px var(--ck-color-widget-hover-border),
			0 0 40px var(--ck-color-widget-hover-border);
	}

	100% {
		opacity: 1;
		transform: scale(1);
		color: inherit;
		text-shadow: none;
	}
}

@media (prefers-reduced-motion: reduce) {
	.ck-slash {
		animation: none;
		opacity: 1;
		text-shadow: none;
	}
}

.cancel-request {
	background-color: hsl(0, 0%, 98%) !important;
	border: 1px solid hsla(0, 0%, 0%, 0.15);
	border-radius: .375rem;
	cursor: pointer;
	left: 0;
	margin: 0 auto !important;
	padding: 5px 10px !important;
	position: absolute !important;
	right: 0;
	top: 45px;
	width: fit-content !important;
	box-shadow: 0px 2px 4px hsla(0, 0%, 0%, 0.1);
	z-index: 1;
}

.cancel-request .ck-button__label {
	color: hsl(0, 0%, 36%) !important;
	font-weight: 500 !important;
	font-size: 14px !important;
	line-height: 14px;
}